const sql = require('mssql/msnodesqlv8')
const createSearchResult = require('../cosmetic-box.hbs')

const config = {
  connectionString:
    'Driver={ODBC Driver 17 for SQL Server};Server=DESKTOP-V70N1TR\\SQLEXPRESS;Database=FortniteHelper;Trusted_Connection=Yes;',
};

async function run() {
  let conn
  try{
    conn = await sql.connect(config);

    const result = await new sql.Request()
      .query(
        `SELECT TOP 40 l.itemID, b.Images as brI, ins.Images as insI, t.AlbumArt as trI, leg.Images as legI, c.Images as carI
         FROM Items l
                join BR b on l.BRID = b.BRID
                join Festival f on l.FestivalID = f.FestivalID
                join Instruments ins on f.InstrumentsID = ins.InstrumentsID
                join Tracks t on f.TracksID = t.TracksID
                join Lego leg on l.LegoID = leg.LegoID
                join Cars c on l.CarsID = c.CarsID
         ORDER BY AddedDate DESC;`);


    console.log(result);
  }catch(error){
    console.error('Error inserting data:', error);
  } finally {
    if (conn) await sql.close();
  }

}

function insertSearchResult(result) {
  const divResult = document.querySelector('.cosmetic-search-result')
  for (const item of result.recordset) {
    divResult.insertAdjacentHTML('beforeend', createSearchResult(item));
  }

}

run()
